import React from "react";
import styled from "styled-components";
import CategoryCard from "../Category/CategoryCard";
import MovieCard from "../MovieCard/MovieCard";

const Container = styled.div`
  margin: 50px 0px;
`;
const TopSection = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: fit-content;
`;
const BlockDescription = styled.h1`
  color: black;
  font-family: century-gothic, sans-serif;
  font-style: italic;
  font-size: 32px;
  font-weight: 400;
  margin: 0;
  margin-right: 20px;
`;

const Body = styled.div`
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: fit-content;
`;

const CategoryBlock = ({ Description = "", category = "" }) => {
  // ({ Description = "", category = "" , MovieTitle = {MovieTitle} , Year = {Year} , MovieDuration = {MovieDuration} , MovieCategory  = {MovieCategory}})
  return (
    <Container>
      {category === "" ? null : (
        <TopSection>
          <BlockDescription>{Description}</BlockDescription>
          <CategoryCard Text={category} />
        </TopSection>
      )}
      <Body>
        <MovieCard
          MovieTitle="Vikings"
          Year="2021"
          MovieDuration="1hr 25min"
          MovieCategory="Series"
        />
      </Body>
    </Container>
  );
};

export default CategoryBlock;
